
* {padding: 0; margin: 0;}
.Channel-logo{
    width: 400px;
    text-align: left;
    max-width: 100%;
    object-fit: cover;
}
.flex-container  {
  display: flex;
  justify-content: space-between;
  align-items: center;
      background-image: url('loginfo.png');
      background-repeat: no-repeat;
      padding: auto;
      font-size: larger;

}

.logo {
  margin: 0 0 0 .45em;
}
    .logo a {
      color: yellow;
    }

    .logo, .navigation {
        list-style: none;
        text-transform: uppercase;
    }

    .logo a, .navigation a {
        text-decoration: none;
        letter-spacing: .45rem;
    }

.navigation  {
  display: flex;
}

.navigation a {
  display: block;
  padding: 1em;
  color:white;
}

.navigation a:hover {
    background: #00ace6;
}

/* *********** media queries *********** */
@media all and (max-width: 990px) {
  .flex-container {
    flex-direction: column;
  }
  
  .logo {
  margin: 0;
}
  
  .navigation  {
    width: 100%;
    justify-content: space-around;
  }
}


/* ****** ****** */
@media all and (max-width: 600px) {
 
  .logo {
    margin: .25em 0;
    align-self: left; /* align the logo to the left side of 'flex-container' */
}
  
  .navigation {
    flex-direction: column;
  }
  
  .navigation a { 
    text-align: center; 
    padding: 10px;
    border-top: 1px solid rgba(255,255,255,0.3); 
    border-bottom: 1px solid rgba(0,0,0,0.1); 
  }
  
  .navigation li:last-of-type a {
    border-bottom: none;
  }
}
h1.Channel-logo{
    width: 20px;
}